body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

nav ul {
  display: flex;
  list-style-type: none;
  padding: 0;
}

nav li {
  margin-right: 20px;
}

nav a {
  text-decoration: none;
  color: black;
}

footer {
  text-align: center;
  padding: 20px;
  background-color: #f1f1f1;
}

.container {
  padding: 20px;
}
.about-section {
  padding: 20px;
}

.about-section h2 {
  margin-top: 20px;
  font-size: 24px;
}

.about-section ul {
  list-style-type: none;
  padding: 0;
}

.about-section li {
  margin-bottom: 10px;
}
/* src/components/Contact.css */
.contact-section {
  padding: 20px;
  text-align: center;
}

.contact-section h2 {
  font-size: 24px;
  margin-bottom: 10px;
}

.contact-section p {
  font-size: 18px;
  margin-bottom: 20px;
}

.contact-section a {
  font-size: 20px;
  color: #007BFF;
  text-decoration: none;
}

.contact-section a:hover {
  text-decoration: underline;
}
